@import "../../sass/variables.scss";
@import "../../sass/mixins.scss";

.modal-blue__rules::-webkit-scrollbar {
    width: 3px; }
  
  .modal-blue__rules::-webkit-scrollbar-track-piece {
    background-color: #F2F2F2; }
  
  .modal-blue__rules::-webkit-scrollbar-thumb {
    background-color: #D9D9D9; }
  
.modal_container {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal_back {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.65);
}
.modal-blue__window {
  width: 70%;
  max-width: 27.5rem;
  background: #fff;
  position: relative;
  @media (max-width: $mobile_l) {
    margin-top: 40px;
  }
}
.modal-blue__close {
  position: absolute;
  cursor: pointer;
  top: -1.3rem;
  right: -1.3rem;
  span {
    width: 3px;
    height: 2rem;
    display: block;
    transform: rotate(135deg);
    background: #fff;
    z-index: 999;
    position: absolute;
    top: 0;
    left: 0;
    &:nth-child(2) {
      transform: rotate(45deg);
    }
  }
}

.modal-blue__window--pink > .modal-blue__close {
  // background-image: url(../images/close_blue_2.svg);
}
.modal-blue__header {
  background: $blue;
  text-align: center;
  color: #fff;
  font-size: 1.0625rem;
  line-height: 1.0625rem;
  padding: 1.25rem 0;
  font-weight: 500;
  @media (max-width: $mobile_l) {
    font-size: 14px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
.modal-blue__content {
  padding: 1.875rem 1.5625rem;
  @media (max-width: $mobile_l) {
    padding: 20px;
  }
}
.modal-blue__rules {
  text-align: left;
  max-height: 18.75rem;
  overflow: auto;
  margin: 1.25rem 1.875rem;
  @media (max-width: $mobile_l) {
    margin: 0;
  }
}
.modal-blue__textrules {
  font-size: 0.75rem;
  line-height: 0.9375rem;
  overflow-y: hidden;
  padding-right: 0.625rem;
}
